
.radio {
    margin-right: 10px;
    width: 20px;
    height: 20px;
    position: relative;
    cursor:pointer;
}

.radio::before {
    content: '';
    display: inline-block;
    position: absolute;
    width: 18px;
    height: 18px;
    background-color: transparent;
    border-radius: 50%;
    border: 1px solid black;
}

.radio:checked::before {
    content: '';
    display: inline-block;
    position: absolute;
    width: 18px;
    height: 18px;
    background-color: #0066CC;
    border-radius: 50%;
    border: 1px solid black;
}

.label {
    margin: 10px;
    display: flex;
    min-height: max-content;
    cursor: pointer;
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
}

.options {
    margin-left: 127px;
    display: flex;
    flex-direction: column;
    
}
